/* INDIVIDUAL CARD STYLE */
.card-indiv-div{
    border-radius: 10px;
    position: relative;
    padding: 10px 0px;
    overflow: hidden;
    transition: 0.5s;
}
.card-indiv-header {
    position: absolute;
    top:0%;
    left: -4%;
    padding: 0px 20px;
    transform: skewX(-30deg);
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.active-indicator{
    height: 12px;
    width: 12px;
    transform: skewX(30deg);
    border-radius: 50%;
    margin-left: 10px;
}
.card-indiv-header h2{
    transform: skewX(30deg);
    margin: 5px 10px 5px 10px;
}
.card-indiv-footer {
    position: absolute;
    bottom:0%;
    right: -4%;
    padding: 0px 20px;
    transform: skewX(-30deg);
    border-radius: 5px;
}
.card-indiv-icons-div{
    transform: skewX(30deg);
    margin: 5px 10px;
    color: #fff;
}
.card-icons{
    margin-right: 5px;
    transition: 0.5s;
}
.card-icons:hover{
    color: #3578E5 !important;
    transition: 0.5s;
}
.card-indiv-body{
    border-radius: 5px;
    overflow: hidden;
}
.card-indiv-body img{
    width: 100%;
    height: 250px;
    object-fit: cover;
}
.slick-prev:before , .slick-next:before{
    color : gray !important;
    font-size: 25px;
    transition: 0.5;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
    visibility: hidden !important;
    transition: 0.5;
}
.ant-switch-checked {
    background-color: #4f4f4f !important;
}

/* ABOUT ME STYLES */
.profile-container{
    padding: 0px 10px;
    position: relative;
    overflow: hidden;
}
.profile-card-div {
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    /* height: 500px; */
}
.profile-card-header-title{
    margin: 0;
    /* transform: skewX(-35deg); */
    position: absolute;
    top: 0%;
    left: 0%;
    border-bottom-right-radius: 10px;
}
.profile-card-header-title h2{
    /* transform: skewX(35deg); */
    margin: 0px 20px 5px 20px;
}
.profile-card-footer-title{
    height: 35px;
    width: 35px;
    margin: 0;
    position: absolute;
    bottom: 4%;
    right: 3%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile-dp-cont{
    border-radius: 50%;
    overflow: hidden;
    /* margin: 0px 50px; */
}
.about-me-left-grid{
    position: relative;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 40px;
}
.about-me-right-grid h4{
    font-weight: bold;
    font-size: 20px;
}
.profile-dp-cont img , .profile-dp-bg{
    height: auto;
    width: 150px;
}
.profile-dp-bg{
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 30%;
}

/* exp */
.exp-left-side{
    padding: 10px 20px;
}
.exp-left-side p {
