}

.skill-label{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: 0.5s;
    display: flex;
    align-items: center;
    justify-content: center;
}
.skill-div:hover .skill-label{
    opacity: 1;
    transition: 0.5s;
}
.skill-img{
    transform: scale(1);
    transition: 0.5s;
}
.skill-div:hover > .skill-img{
    transform: scale(0.8);
    transition: 0.5s;
}

.customTextField{
    display: flex;
    align-items: center;
