.card-project-div{
    padding: 1%;
    width: 100%;
    height: 10vw;
    border-radius: 1vw;
    background-color: #fff;
    overflow: hidden;
    position: relative;
    text-align: center;
    transform: scale(1);
    transition: 0.5s;
}
.card-project-div img{
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50% , -50%) scale(1);
    opacity: 1;
    transition: 0.5s;
}
.card-project-div p{
    color: #2D3D66 !important;
    margin: 0;
    font-weight: 600;
    font-size: 1vw;
}
.card-project-div .company-name{
    font-size: 2vw;
}
.card-text-content{
    position: absolute;
    top: 50%;
    left: -100%;
    width: 100%;
    transition: 0.5s;
    transform: translate(-50% , -50%);
}

.card-project-div:hover img{
    transition: 0.5s;
    transform: translate(-50% , -50%) scale(2);
    opacity: 0.2;
}
.card-project-div:hover .card-text-content{
    transition: 0.5s;
    left: 50%;
}
.card-project-div:hover {
    transform: scale(1.1);
    transition: 0.5s;
}
.custom-text-field{
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-bottom: 1vw;
    margin-top: 1vw;
